import MyClass from "./my-class/my-class";
import MyDate from "./my-date/my-date";


export default {
   components: {

    MyDate,
    MyClass
  },
  data() {
    return {};
  },
  created() {
    this.viewFlag = false;
  },
  /**
   * 生命周期函数--监听页面显示
   */
  onShow() {
    if (this.viewFlag) {
      this.$refs.redDot._getNotifyCategories();
    }
    //  第一条未完成任务 返回更新
    this.$refs.updateFirstTask.updateFirstTask();
    this.$refs.updateFirstTask.getWeekDateState();//一周状态

  },
  methods: {}
 
};
